.mail {
  padding-top: 10.416vw;
  padding-bottom: 10.416vw;
  background-color: #fff;
  overflow: hidden;
  position: relative;


  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(19,29,74, 0.9) url(../assets/images/Spin-2s-100px.svg) center / 50px no-repeat;
    opacity: 0;
    visibility: hidden;
    transition: all 0.5s ease 0s;
  }

  &._sending::after {
    opacity: 1;
    visibility: visible;
  }

  @media (max-width: $md1) {
    padding-top: 18.75vw;
    padding-bottom: 18.75vw;
  }

  .container {
    .abs-wrp {
      margin-bottom: 5.2083vw;

      @media (max-width: $md1) {
        margin-bottom: 12.5vw;
      }

      h2 {
        color: #1C2757;
        margin-bottom: 0;
      }
    }

    .mail-form {
      width: 60.364583vw;
      margin: 0 auto;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;

      @media (max-width: $md1) {
        width: 100%;
        flex-direction: column;
      }

      .item {
        color: #1C2757;
        font-size: 1.25vw;
        margin-bottom: 1.5625vw;
        display: flex;
        flex-direction: column;

        @media (max-width: $md1) {
          font-size: 3.75vw;
          line-height: 1.6;
          margin-bottom: 3.125vw;
        }

      }

      input,
      textarea {
        border: none;
        border-bottom: 1px solid #ff6d56;
        background-color: inherit;
        resize: none;
        padding: 1.0416vw;
        font-size: 1.25vw;
        color: #1C2757;

        @media (max-width: $md1) {
          font-size: 3.75vw;
          line-height: 1.6;
          padding: 1.5625vw;
        }

        &._error {
          box-shadow: 0 15px 15px -15px red;
        }
      }

      .small {
        width: 45%;

        @media (max-width: $md1) {
          width: 100%;
          margin-bottom: 6.25vw;
        }
      }

      .big {
        width: 100%;
        margin-bottom: 3.125vw;

        @media (max-width: $md1) {
          margin-bottom: 6.25vw;
        }
      }

      .center {
        width: 100%;        

        button {
          font-family: 'GothamPro';
          font-size: .9375vw;
          line-height: 1.875;
          margin: 0 auto;
          width: 14.0625vw;
          height: 3.125vw;
          background-color: #ff6d56;
          display: flex;
          align-items: center;
          justify-content: center;
          color: #fff;
          border: none;
          border-radius: 15px;
          cursor: pointer;
          text-transform: uppercase;
          transition: 0.5s;

          @media (max-width: $md1) {
            width: 100%;
            height: 12.5vw;
            font-size: 3.75vw;
            line-height: 1.6;
          }

          &:hover {
            background-color: #1C2757;
          }
        }
      }
    }
  }
}