.about {
  background-color: #fff;
  padding-top: 10.416vw;
  padding-bottom: 10.416vw;
  overflow: hidden;

  @media (max-width: $md1) {
    padding-top: 18.75vw;
    padding-bottom: 31.25vw;
  }

  .container {

    .abs-wrp {
      

      h2 {
        color: #1C2757;
      }

      p {
        color: #1C2757;
      }
    }

    .items {
      width: 100%;
      display: flex;
      justify-content: space-between;

      .item {
        padding: 3.125vw 1.0416vw;
        width: 27.60416vw;
        background-color: #1C2757;
        display: flex;
        flex-direction: column;
        align-items: center;
        box-shadow: 0px 0px 30px rgba(255, 255, 255, 0.2);

        @media (max-width: $md1) {
          width: auto;
          justify-content: center;
          height: 78.125vw;
          padding: 0 6.25vw;
        }
  
        .icon {
          width: 2.916vw;
          height: 2.916vw;
          margin-bottom: 1.6vw;

          @media (max-width: $md1) {
            width: 12.5vw;
            height: 12.5vw;
            margin-bottom: 10vw;
          }

          svg {
            width: 100%;
            height: 100%;
          }
        }
  
        h3 {
          font-size: 1.25vw;
          letter-spacing: 0.3125vw;
          color: #fff;
          margin-bottom: 1.6vw;

          @media (max-width: $md1) {
            font-size: 4.375vw;
            line-height: 1.42857;
            text-align: center;
            margin-bottom: 6.875vw;
          }
        }
  
        p {
          text-align: center;
          color: #fff;

        }
      }

      @media (max-width: $md1) {
        .slick-arrow {
          position: absolute;
          bottom: -21.875vw;
          width: 16.25vw;
          height: 12.1875vw;
          background-repeat: no-repeat;
          background-size: 100%;
  
          &.prev {
            left: 0;
            background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='51' height='39' viewBox='0 0 51 39' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M50.9599 0H0V39H50.9599V0Z' fill='%231C2757'/%3e%3cpath d='M23.2721 19.3384L28.1371 23.7292' stroke='%23F3F6F9' stroke-width='0.672414' stroke-linecap='round' stroke-linejoin='round'/%3e%3cpath d='M23.2721 19.3883L28.1371 14.9974' stroke='%23F3F6F9' stroke-width='0.672414' stroke-linecap='round' stroke-linejoin='round'/%3e%3c/svg%3e ");
          }
  
          &.next {
            right: 0;
            background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='52' height='39' viewBox='0 0 52 39' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M51.9203 0H0.960449V39H51.9203V0Z' fill='%231C2757'/%3e%3cpath d='M28.6484 19.3384L23.7834 23.7292' stroke='%23F3F6F9' stroke-width='0.672414' stroke-linecap='round' stroke-linejoin='round'/%3e%3cpath d='M28.6484 19.3883L23.7834 14.9974' stroke='%23F3F6F9' stroke-width='0.672414' stroke-linecap='round' stroke-linejoin='round'/%3e%3c/svg%3e ");
          }
        }
      }
    }
  }
}