.features {
  padding-top: 10.416vw;
  padding-bottom: 10.416vw;
  background-color: #fff;
  overflow: hidden;


  @media (max-width: $md1) {
    padding-top: 18.75vw;
    padding-bottom: 18.75vw;
  }

  .container {
    .abs-wrp {
      margin-bottom: 5.2083vw;

      @media (max-width: $md1) {
        margin-bottom: 12.5vw;
      }

      h2 {
        color: #1C2757;
        margin-bottom: 0;
      }
    }

    .items {
      display: flex;
      justify-content: space-between;

      @media (max-width: $md1) {
        flex-direction: column;
        
      }

      .item {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 18.22916vw;
        background-color: #1C2757;
        box-shadow: 0px 0px 30px rgba(216, 201, 201, 0.2);
        padding: 2.083vw;

        @media (max-width: $md1) {
          width: auto;
          justify-content: center;
          height: 46.875vw;
          padding: 0;
          margin-bottom: 12.5vw;

          &:last-child {
            margin-bottom: 0;
          }
        }

        .icon {
          width: 5.2083vw;
          height: 5.2083vw;
          margin-bottom: 1.5625vw;

          @media (max-width: $md1) {
            width: 12.5vw;
            height: 12.5vw;
            margin-bottom: 6.875vw;
          }

          svg {
            width: 100%;
            height: 100%;
          }
        }

        h3 {
          font-family: 'GothamPro-Bold';
          font-size: 1.25vw;
          color: #fff;
          margin-bottom: 1.5625vw;

          @media (max-width: $md1) {
            font-size: 4.375vw;
            line-height: 1.42857;
            text-align: center;
            margin-bottom: 3.75vw;
          }
        }

        p {
          font-size: 1.875vw;
          color: #fff;

          @media (max-width: $md1) {
            font-size: 4.375vw;
            line-height: 1.42857;
          }
        }
      }
    }
  }
}