.process {
  // padding-top: 10.416vw;
  padding-bottom: 10.416vw;
  background-color: #fff;
  overflow: hidden;


  @media (max-width: $md1) {
    // padding-top: 18.75vw;
    padding-bottom: 18.75vw;
  }

  .container {
    .abs-wrp {
      margin-bottom: 5.2083vw;

      @media (max-width: $md1) {
        margin-bottom: 12.5vw;
      }

      h2 {
        color: #1C2757;
        margin-bottom: 0;
      }
    }

    .items {
      background-color: #1C2757;
      padding: 5.2083vw 0;

      @media (max-width: 767.98px) {
        padding: 12.5vw 6.25vw;
      }

      ul {
        list-style: none;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        margin-bottom: 4.16vw;

        &:last-child {
          margin-bottom: 0;
        }

        @media (max-width: 767.98px) {
          flex-direction: column;
          margin-bottom: 25vw;
        }


        &.revers {
          flex-direction: row-reverse;

          @media (max-width: 767.98px) {
            flex-direction: column;
          }

          li::after {
            left: -3.125vw;
          }
        }

        li {
          width: 19.7916vw;
          display: flex;
          align-items: center;
          flex-direction: column;
          position: relative;

          @media (max-width: 767.98px) {
            flex-direction: column;
            margin-bottom: 25vw;
            width: 100%;
          }

          &::after {
            content: '';
            position: absolute;
            right: -3.125vw;
            top: calc(50% - 0.9114583vw);
            background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='512' height='248' viewBox='0 0 512 248' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M506.134 109.843C506.128 109.837 506.123 109.83 506.116 109.824L401.612 5.82394C393.783 -1.96706 381.12 -1.93807 373.327 5.89193C365.535 13.7209 365.565 26.3839 373.394 34.1759L443.558 104H20C8.954 104 0 112.954 0 124C0 135.046 8.954 144 20 144H443.557L373.395 213.824C365.566 221.616 365.536 234.279 373.328 242.108C381.121 249.939 393.785 249.966 401.613 242.176L506.117 138.176C506.123 138.17 506.128 138.163 506.135 138.157C513.968 130.339 513.943 117.635 506.134 109.843Z' fill='%23FF6D56'/%3e%3c/svg%3e ");
            background-repeat: no-repeat;
            background-size: 100%;
            width: 3.64583vw;
            height: 1.822916vw;

            @media (max-width: 767.98px) {
              width: 9.375vw;
              height: 4.6875vw;
              transform: rotate(90deg);
              right: calc(50% - 4.6875vw);
              top: unset;
              bottom: -40px;
            }
          }


          &:last-child {
            margin-bottom: 0;

            &::after {
              display: none;
            }
          }

          &.down::after {
            display: block;
            transform: rotate(90deg);
            right: calc(50% - 1.822916vw);;
            top: unset;
            bottom: -2.083vw;

            @media (max-width: 767.98px) {
              right: calc(50% - 4.6875vw);
              top: unset;
              bottom: -40px;
            }
          }

          &.left::after {
            transform: rotate(180deg);
            @media (max-width: 767.98px) {
              transform: rotate(90deg);
              left: calc(50% - 4.6875vw);
              top: unset;
              bottom: -40px;
            }
          }

          svg {
            width: 4.16vw;
            height: 4.16vw;
            margin-bottom: 1.5625vw;

            @media (max-width: 767.98px) {
              width: 12.5vw;
              height: 12.5vw;
              margin-bottom: 6.875vw;
            }
          }

          p {
            text-align: center;
            font-size: 0.72916vw;
            width: 14.0625vw;

            @media (max-width: 767.98px) {
              width: 100%;
              font-size: 4.375vw;
              line-height: 1.42857;
              margin-left: 0;
              text-align: center;
            }
          }
        }
      }
    }
  }
}