.modal {
  display: none;
  position: relative;
  padding: 0 !important;
  width: 31.25vw;

  @media (max-width: $md1) {
    width: 100%;
  }

  .close {
    position: absolute;
    width: 1.14583vw;
    height: 1.14583vw;
    right: 1.0416vw;
    top: 1.0416vw;
    cursor: pointer;

    @media (max-width: $md1) {
      width: 6.875vw;
      height: 6.875vw;
      right: 6.25vw;
      top: 6.25vw;
    }

    svg {
      width: 100%;
      height: 100%;

      path {
        fill: #fff;
      }
    }
  }

  .cnt-wrp {
    .top-wrp {
      background-color: #1C2757;
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
      padding: 2.083vw;

      @media (max-width: $md1) {
        padding: 6.25vw;
      }

      h3 {
        font-size: 1.0416vw;
        text-transform: uppercase;
        margin-bottom: 1.0416vw;

        @media (max-width: $md1) {
          font-size: 3.75vw;
          line-height: 1.6;
          margin-bottom: 3.125vw;
        }
      }

      p {
        font-family: 'GothamPro-Bold';
        font-size: 2.5vw;

        @media (max-width: $md1) {
          font-size: 7.5vw;
          line-height: 1.6;
      }

        span {
          font-size: 1.25vw;

          @media (max-width: $md1) {
            font-size: 3.75vw;
            line-height: 1.6;
          }
        }
      }
    }

    .inf-wrp {
      display: flex;
      flex-direction: column;
      width: 100%;
      padding: 2.083vw 3.125vw;
      color: #1C2757;

      @media (max-width: $md1) {
        padding: 6.25vw;
      }

      h4 {
        font-size: 1.0416vw;
        text-transform: uppercase;
        margin-bottom: 1.0416vw;

        @media (max-width: $md1) {
          font-size: 3.75vw;
          line-height: 1.6;
          margin-bottom: 3.125vw;
        }
      }

      ul {
        list-style: none;
        padding-left: 0.9375vw;
        margin-bottom: 1.0416vw;

        @media (max-width: $md1) {
          padding-left: 4.0625vw;
          margin-bottom: 5vw;
        }

        li {
          position: relative;
          font-family: 'GothamPro-Light';
         
          @media (max-width: $md1) {
            margin-bottom: 5vw;
          
            &:last-child {
              margin-bottom: 0;
            }
          }
          

          &:before {
            content: '';
            position: absolute;
            left: -0.9375vw;
            top: 0.52083vw;
            width: 0.364583vw;
            height: 0.364583vw;
            background-color: #1C2757;
            border-radius: 50%;

            @media (max-width: $md1) {
              left: -4.0625vw;
              width: 1.5625vw;
              height: 1.5625vw;
              top: 2.1875vw;
            }
          }              
        }
      }

      h5 {
        font-size: 0.83vw;
        line-height: 1.875;
        margin-bottom: 1.0416vw;

        @media (max-width: $md1) {
          font-size: 3.75vw;
          line-height: 1.6;
          margin-bottom: 3.125vw;
        }
      }

      a {
        margin: 0 auto;
        margin-top: 3.125vw;
        width: 14.0625vw;
        height: 3.125vw;
        background-color: #ff6d56;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #fff;
        border-radius: 15px;
        cursor: pointer;
        text-transform: uppercase;
        transition: 0.5s;

        @media (max-width: $md1) {
          width: 100%;
          height: 12.5vw;
          margin-top: 18.75vw;
        }

        &:hover {
          background-color: #1C2757;
        }
      }

      
    }
    
  }

  &-order {
    position: relative;


    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(19,29,74, 0.9) url(../assets/images/Spin-2s-100px.svg) center / 50px no-repeat;
      opacity: 0;
      visibility: hidden;
      transition: all 0.5s ease 0s;
    }
  
    &._sending::after {
      opacity: 1;
      visibility: visible;
    }

    .close {
  
      svg {
  
        path {
          fill: #1C2757;
        }
      }
    }

    .cnt-wrp {

      width: 100%;
      padding: 2.083vw;

      @media (max-width: $md1) {
        padding: 6.25vw;
      }

      .mail-form {
        width: 100%;
        flex-direction: column;
        display: flex;

        .item {
          color: #1C2757;
          font-size: 1.25vw;
          margin-bottom: 1.5625vw;
          display: flex;
          flex-direction: column;
  
          @media (max-width: $md1) {
            font-size: 3.75vw;
            line-height: 1.6;
            margin-bottom: 3.125vw;
          }
        }

        .type {
          flex-direction: row;
          justify-content: center;

          input {
            font-family: 'GothamPro-Bold';
            border: none;
            padding: 0;
            width: 10.416vw;
            text-align: center;
            user-select: none;

            @media (max-width: $md1) {
              width: 31.25vw;
            }
          }
        }

        p {
          margin-bottom: 1.5625vw;
          color: #1C2757;
          font-size: 0.9375vw;
          @media (max-width: $md1) {
            font-size: 3.125vw;
            line-height: 1.6;
            margin-bottom: 3.125vw;
          }
        }

        input {
          border: none;
          border-bottom: 1px solid #ff6d56;
          background-color: inherit;
          resize: none;
          padding: 1.0416vw;
          font-size: 1.25vw;
          color: #1C2757;

          @media (max-width: $md1) {
            font-size: 3.75vw;
            line-height: 1.6;
            padding: 1.5625vw;
          }

          &._error {
            box-shadow: 0 15px 15px -15px red;
          }
        }

  
  
        .center {
          width: 100%;        
  
          button {
            font-family: 'GothamPro';
            font-size: .9375vw;
            line-height: 1.875;
            margin: 0 auto;
            width: 14.0625vw;
            height: 3.125vw;
            background-color: #ff6d56;
            display: flex;
            align-items: center;
            justify-content: center;
            color: #fff;
            border: none;
            border-radius: 15px;
            cursor: pointer;
            text-transform: uppercase;
            transition: 0.5s;
  
            @media (max-width: $md1) {
              width: 100%;
              height: 12.5vw;
              font-size: 3.75vw;
              line-height: 1.6;
            }
  
            &:hover {
              background-color: #1C2757;
            }
          }
        }
      }
    }
  }
}