.works {
  background-color: #fff;
  padding-top: 10.416vw;
  padding-bottom: 10.416vw;
  overflow: hidden;

  @media (max-width: $md1) {
    padding-top: 18.75vw;
    padding-bottom: 12.5vw;
  }

  .container {
    position: relative;

    #btnMore {
      margin: 0 auto;
      width: 14.0625vw;
      height: 3.125vw;
      background-color: #ff6d56;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #fff;
      border: none;
      border-radius: 15px;
      cursor: pointer;
      text-transform: uppercase;
      transition: .5s;

      &:hover {
        background-color: #1C2757;
      }
    }

    .abs-wrp {
      margin-bottom: 5.2083vw;
  
      @media (max-width: $md1) {
        margin-bottom: 12.5vw;
      }
  
      h2 {
        color: #1C2757;
        margin-bottom: 0;
      }
  
      p {
        color: #1C2757;
      }
    }
    .works__exmp {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 2.083vw;
      overflow: hidden;
  
      @media (max-width: $md1) {
        margin-bottom: 0;
        overflow: unset;
      }
  
      img {
        height: 100%;
        width: 100%;
        object-fit: cover;
        object-position: center;
      }
  
      .item {
        width: 26.0416vw;
        height: 15.625vw;
        margin-right: 6.510416vw;
        margin-bottom: 2.083vw;
        overflow: hidden;
        transition: all 1s ease;
        position: relative;
        z-index: 2;
        box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.2);
  
        @media (max-width: $md1) {
          width: 100%;
          height: 46.875vw;
          margin-right: 0;
          margin-bottom: 0;
        }
  
        &:nth-child(3n) {
          margin-right: 0;
        }
  
        &:hover {
          .desc {
            opacity: 1;
          }
        }
  
        .desc {
          opacity: 0;
          padding: 0 2.083vw;
          width: 100%;
          height: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          background-color: rgba(29, 40, 89, 0.9);
          position: absolute;
          top: 0;
          left: 0;
          transition: all 1s ease;
  
          @media (max-width: $md1) {
            padding: 0 6.25vw;
            opacity: 1;
            background-color: rgba(29, 40, 89, 0.6);
          }
  
          h3 {
            font-size: 1.25vw;
            text-align: center;
            position: relative;
            margin-bottom: 1.35416vw;
            color: #fff;
  
            @media (max-width: $md1) {
              font-size: 5.625vw;
              line-height: 1.4;
              margin-bottom: 6.25vw;
            }
  
            &:after {
              content: '';
              width: 100%;
              height: 2px;
              background-color: #ff6d56;
              position: absolute;
              bottom: 0;
              left: 0;
            }
          }
  
          p {
            color: #fff;
            text-align: center;
          }
        }
  
      }

      @media (max-width: $md1) {
        .slick-arrow {
          position: absolute;
          bottom: -21.875vw;
          width: 16.25vw;
          height: 12.1875vw;
          background-repeat: no-repeat;
          background-size: 100%;
          z-index: 2;
  
          &.prev {
            left: 0;
            background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='51' height='39' viewBox='0 0 51 39' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M50.9599 0H0V39H50.9599V0Z' fill='%231C2757'/%3e%3cpath d='M23.2721 19.3384L28.1371 23.7292' stroke='%23F3F6F9' stroke-width='0.672414' stroke-linecap='round' stroke-linejoin='round'/%3e%3cpath d='M23.2721 19.3883L28.1371 14.9974' stroke='%23F3F6F9' stroke-width='0.672414' stroke-linecap='round' stroke-linejoin='round'/%3e%3c/svg%3e ");
          }
  
          &.next {
            right: 0;
            background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='52' height='39' viewBox='0 0 52 39' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M51.9203 0H0.960449V39H51.9203V0Z' fill='%231C2757'/%3e%3cpath d='M28.6484 19.3384L23.7834 23.7292' stroke='%23F3F6F9' stroke-width='0.672414' stroke-linecap='round' stroke-linejoin='round'/%3e%3cpath d='M28.6484 19.3883L23.7834 14.9974' stroke='%23F3F6F9' stroke-width='0.672414' stroke-linecap='round' stroke-linejoin='round'/%3e%3c/svg%3e ");
          }
        }
      }
    }

    @media (max-width: $md1) {
      .count-slide {
        position: relative;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        height: 31.25vw;
        padding-right: 0;
        color: #1C2757;

        .big {
          font-size: 6.25vw;
        }

        .line {
          width: 5.7625vw;
          transform: rotate(-77.47deg);
          height: 1px;
          background-color: #1C2757;
        }

        .small {
          font-size: 4.375vw;
      }
      }
    }
  }

}