@keyframes bounce-2 {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }

  50% {
    -webkit-transform: translateY(50%);
    transform: translateY(50%);
  }

  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

.hero {
  height: 100vh;
  position: relative;
  background: linear-gradient(180deg, rgba(37, 125, 191, 0.17) 0%, #131D4A 100%);
  overflow: hidden;

  .large-header {
    background-image: url("assets/images/demo-bg.jpg");
    position: absolute;
    width: 100%;
    overflow: hidden;
    background-size: cover;
    background-position: center center;
  }


  .container {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;

    h1 {
      font-family: 'GothamPro-Bold';
      font-size: 3.75vw;
      color: #fff;
      text-transform: uppercase;
      letter-spacing: 0.625vw;
      margin-bottom: 2.083vw;

      @media (max-width: $md1) {
        font-size: 6.25vw;
        line-height: 1.4;
        text-align: center;
        margin-bottom: 12.5vw;
      }
    }

    p {
      font-size: 1.875vw;
      color: #fff;
      text-transform: uppercase;
      letter-spacing: 0.625vw;

      @media (max-width: $md1) {
        font-size: 4.375vw;
        line-height: normal;
        letter-spacing: normal;
        text-align: center;
      }
    }
  }

  .scroll {
    position: absolute;
    bottom: 2.60416vw;
    left: calc(50% - 0.72916vw);
    width: 1.4583vw;
    height: 2.60416vw;
    animation-name: bounce-2;
    animation-timing-function: ease;
    animation-duration: 3s;
    animation-iteration-count: infinite;

    @media (max-width: $md1) {
      display: none;
    }

    svg {
      width: 100%;
      height: 100%;
    }

    &-mob {
      display: none;

      @media (max-width: $md1) {
        display: block;
        width: 8vw;
        height: auto;
        bottom: 8vw;
        position: absolute;
        left: calc(50% - 4vw);
        animation-name: bounce-2;
        animation-timing-function: ease;
        animation-duration: 3s;
        animation-iteration-count: infinite;
      }

      svg {
        width: 100%;
        height: 100%;
      }
    }
  }
}