.plan {
  padding-top: 10.416vw;
  padding-bottom: 10.416vw;
  background: linear-gradient(180deg, rgba(37, 125, 191, 0.17) 0%, #131D4A 100%);
  overflow: hidden;

  @media (max-width: $md1) {
    padding-top: 18.75vw;
    padding-bottom: 31.25vw;
  }

  .container {

    .abs-wrp {
      margin-bottom: 5.2083vw;
    }

    .items {
      display: flex;
      justify-content: space-around;

      .item {
        width: 20.83vw;
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color: #fff;
        box-shadow: 0px 0px 30px rgba(255, 255, 255, 0.1);

        @media (max-width: $md1) {
          width: 100%;
        }

        &__top {
          background-color: #1C2757;
          display: flex;
          flex-direction: column;
          align-items: center;
          width: 100%;
          padding: 2.083vw;

          @media (max-width: $md1) {
            padding: 6.25vw;
          }

          h3 {
            font-size: 1.0416vw;
            text-transform: uppercase;
            margin-bottom: 1.0416vw;

            @media (max-width: $md1) {
              font-size: 3.75vw;
              line-height: 1.6;
              margin-bottom: 3.125vw;
            }
          }

          p {
            font-family: 'GothamPro-Bold';
            font-size: 2.5vw;

            @media (max-width: $md1) {
              font-size: 7.5vw;
              line-height: 1.6;
          }

            span {
              font-size: 1.25vw;

              @media (max-width: $md1) {
                font-size: 3.75vw;
                line-height: 1.6;
              }
            }
          }
        }

        &__bottom {
          display: flex;
          flex-direction: column;
          align-items: center;
          width: 100%;
          padding: 2.083vw;

          @media (max-width: $md1) {
            padding: 6.25vw;
          }

          p {
            font-family: 'GothamPro';
            font-size: 1.0416vw;
            display: flex;
            align-items: center;
            color:#666;
            margin-bottom: 1.0416vw;

            @media (max-width: $md1) {
              font-size: 3.75vw;
              line-height: 1.6;
              margin-bottom: 3.125vw;
            }

            span {
              font-family: 'GothamPro-Bold';
              margin-right: 0.52083vw;
              color: #1C2757;

              @media (max-width: $md1) {
                font-size: 3.75vw;
                line-height: 1.6;
                margin-right: 1.5625vw;
              }
            }

            svg {
              width: 1.25vw;
              height: 1.25vw;
              margin-right: 0.52083vw;

              @media (max-width: $md1) {
                width: 4.375vw;
                height: 4.375vw;
                margin-right: 1.5625vw;
              }
            }
          }

          a {
            margin-top: 2.083vw;
            width: 10.416vw;
            height: 3.125vw;
            background-color: #ff6d56;
            display: flex;
            align-items: center;
            justify-content: center;
            color: #fff;
            border-radius: 15px;
            cursor: pointer;
            text-transform: uppercase;
            transition: 0.5s;

            @media (max-width: $md1) {
              width: 100%;
              height: 12.5vw;
              margin-top: 6.25vw;
            }

            &:hover {
              background-color: #1C2757;
            }
          }
        }

        &:hover  {
          .item__bottom {
            p {
              span{
                color: #ff6d56;
              }

              svg path{
                fill: #ff6d56;
              }
            }
          }
        } 
      }

      @media (max-width: $md1) {
        .slick-arrow {
          position: absolute;
          bottom: -21.875vw;
          width: 16.25vw;
          height: 12.1875vw;
          background-repeat: no-repeat;
          background-size: 100%;
  
          &.prev {
            left: 0;
            background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='98' height='75' viewBox='0 0 98 75' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M97.9997 0H0V75H97.9997V0Z' fill='white'/%3e%3cpath d='M44.7537 37.1921L54.1095 45.6361' stroke='%231C2757' stroke-width='1.2931' stroke-linecap='round' stroke-linejoin='round'/%3e%3cpath d='M44.7537 37.2881L54.1095 28.8441' stroke='%231C2757' stroke-width='1.2931' stroke-linecap='round' stroke-linejoin='round'/%3e%3c/svg%3e ");          }
  
          &.next {
            right: 0;
            background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='98' height='75' viewBox='0 0 98 75' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M97.9997 0H0V75H97.9997V0Z' fill='white'/%3e%3cpath d='M53.2463 37.1923L43.8905 45.6362' stroke='%231C2757' stroke-width='1.2931' stroke-linecap='round' stroke-linejoin='round'/%3e%3cpath d='M53.2463 37.2882L43.8905 28.8442' stroke='%231C2757' stroke-width='1.2931' stroke-linecap='round' stroke-linejoin='round'/%3e%3c/svg%3e ");          }
        }
      }
    }
  }
}