@charset "UTF-8";

// <Переменные>==============================================================================================================================
$maxWidthContainer: 1721px;
$md1: 767.98px;


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    outline: none;
}

.container{
    width: 91.14583vw;
    margin: 0 auto;

  @media (max-width: $md1) {
    width: 100%;
    padding-left: 9.375vw;
    padding-right: 9.375vw;
  }
}

body {
    font-family: 'GothamPro';
    font-size: 0.9375vw;
    line-height: 1.875;
    color: #fff;
    overflow-x: hidden;

    @media (max-width: $md1) {
        font-size: 3.75vw;
        line-height: 1.6;
    }
}

.wrap_page {
    position: relative;

    .video-wrp {
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: -2;
    }
  
    #myVideo {
      position: fixed;
      top: 0;
      left: 0;
      width: 100vw;
      height: 100vh;
      object-fit: cover;
    }
}



.abs-wrp {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 3.64583vw;

    @media (max-width: $md1) {
        margin-bottom: 12.5vw;
    }

    .decl {
        width: 3.64583vw;
        height: 2px;
        background-color: #FF6D56;
        margin-bottom: 2.083vw;

        @media (max-width: $md1) {
            width: 15.625vw;
            margin-bottom: 3.125vw;
        }
    }

    h2 {
        font-size: 2.864583vw;
        line-height: 1;
        letter-spacing: 0.3125vw;
        color: #fff;
        margin-bottom: 2.083vw;
        text-transform: uppercase;

        @media (max-width: $md1) {
            font-size: 5.625vw;
            line-height: 1.4;
            margin-bottom: 6.25vw;
            text-align: center;
        }
    }

    p {
        font-size: 1.25vw;
        line-height: 1.5;
        color: #fff;
        text-align: center;

        @media (max-width: $md1) {
            font-size: 3.75vw;
            line-height: 1.6;
        }
    }
}

.preloader {
    /*фиксированное позиционирование*/
    position: fixed;
    /* координаты положения */
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    /* фоновый цвет элемента */
    background: #131D4A;
    /* размещаем блок над всеми элементами на странице (это значение должно быть больше, чем у любого другого позиционированного элемента на странице) */
    z-index: 9999999;

    &__row {
        position: relative;
        top: calc(50% - 7.8125vw);
        height: 24.625vw;
        text-align: center;
        overflow-y: hidden;

        @media (max-width: $md1) {
            top: calc(50% - 23.4375vw);
            left: calc(50% - 23.4375vw);
            width: 46.875vw;
            height: 84.375vw;
        }
    }

    &__item {
        position: absolute;
        display: inline-block;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        svg {
            width: 22.083vw;
            height: 9.6875vw;
            animation: pulse 3s infinite linear;
            @media (max-width: $md1) {
                width: 100%;
                height: auto;
            }
        }

        .visible-wrp .slogan-wrp .slogan {
            color: #c9b5ba;
            font-family: "Gilroy-Regular";
            font-size: 2.5vw;
            font-weight: 400;
            height: 9.2vw;
            margin-top: 6.7595vw;
            @media (max-width: $md1) {
                color: #c9b5ba;
                font-family: "Gilroy-Regular";
                font-size: 4.5vw;
                font-weight: 400;
                height: 9.2vw;
                margin-top: 6.7595vw;
            }
        }
    }
}
  
@keyframes pulse {
    100% {
        opacity: 0.9;
    }
    10% {
        opacity: 0.9;
    }
    0% {
        opacity: 0.0001;
    }
}
    
.loaded_hiding .preloader {
    transition: 0.3s opacity;
    opacity: 0;
}

.loaded .preloader {
    display: none;
}

.mob {
    display: none;

    @media (max-width: $md1) {
        display: block;
    }
}

.mob-none {
    @media (max-width: $md1) {
        display: none;
    }
}

textarea,
input[type="text"],
input[type="button"],
input[type="submit"] {
     -webkit-appearance: none;
     border-radius: 0;
}

@import "sass/fonts";
@import "sass/modal";
@import "sass/header";
@import "sass/footer";
@import "sass/hero";
@import "sass/about";
@import "sass/quote";
@import "sass/works";
@import "sass/skills";
@import "sass/features";
@import "sass/plan";
@import "sass/mail";
@import "sass/process";