.skills {
  background: linear-gradient(180deg, rgba(37, 125, 191, 0.17) 0%, #131D4A 100%);
  padding-top: 10.416vw;
  padding-bottom: 10.416vw;
  overflow: hidden;

  @media (max-width: $md1) {
    padding-top: 18.75vw;
    padding-bottom: 18.75vw;
  }

  .container {
    .items {
      height: 31.25vw;
      display: flex;
      justify-content: space-between;

      @media (max-width: $md1) {
        height: auto;
        flex-direction: column;
      }
  
      .left {
        background-color: #fff;
        width: 26.0416vw;
        padding: 4.427083vw;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        @media (max-width: $md1) {
          width: 100%;
          padding: 12.5vw 6.25vw;
          margin-bottom: 12.5vw;
        }
  
        .img {
          border-radius: 50%;
          width: 10.416vw;
          height: 10.416vw;
          margin-bottom: 2.083vw;

          @media (max-width: $md1) {
            width: 25vw;
            height: 25vw;
            margin-bottom: 12.5vw;
          }
  
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 50%;
          }
        }
  
        h3 {
          font-size: 1.25vw;
          text-align: center;
          position: relative;
          margin-bottom: 0.52083vw;
          color: #1C2757;

          @media (max-width: $md1) {
            font-size: 4.375vw;
            line-height: 1.6;
            margin-bottom: 3.125vw;
          }
  
  
          &:after {
            content: '';
            width: 100%;
            height: 2px;
            background-color: #ff6d56;
            position: absolute;
            bottom: 0;
            left: 0;
          }
        }
  
        p {
          color: #1C2757;
        }
      }
  
      .right {
        background-color: #fff;
        width: 57.2916vw;
        padding: 4.427083vw;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: flex-start;

        @media (max-width: $md1) {
          width: 100%;
          padding: 12.5vw 6.25vw;
        }
  
        h3 {
          font-size: 1.25vw;
          position: relative;
          margin-bottom: 2.083vw;
          color: #1C2757;

          @media (max-width: $md1) {
            font-size: 4.375vw;
            line-height: 1.6;
            margin-bottom: 3.125vw;
          }
  
          &:after {
            content: '';
            width: 100%;
            height: 2px;
            background-color: #ff6d56;
            position: absolute;
            bottom: 0;
            left: 0;
          }
  
        }
  
        .skill {
          display: flex;
          justify-content: space-between;
          align-items: center;
          width: 100%;
  
          p {
            color: #1C2757;
            width: 10.416vw;

            @media (max-width: $md1) {
              width: 20.625vw;
            }
          }
  
          .meter {
            width: 26.0416vw;
            height: 0.52083vw;
            position: relative;
            background: #1C2757;

            @media (max-width: $md1) {
              width: 31.25vw;
              height: 3px;
            }
          }
  
          .meter>span {
            display: block;
            height: 100%;
            background-color: #ff6d56;
            box-shadow: inset 0 2px 9px rgba(255, 255, 255, 0.3), inset 0 -2px 6px rgba(0, 0, 0, 0.4);
            position: relative;
            overflow: hidden;
          }

          .persent {
            width: auto;
          }
        }
      }
    }
  }
}