.quote {
  display: flex;
  justify-content: center;
  position: relative;
  background: linear-gradient(180deg, rgba(37, 125, 191, 0.17) 0%, #131D4A 100%);
  overflow: hidden;

  @media (max-width: $md1) {
    padding-top: 18.75vw;
    padding-bottom: 18.75vw;
  }


  .left {
    width: 50vw;
    padding-left: 4.427083vw;
    padding-right: 4.427083vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    @media (max-width: $md1) {
      width: 100%;
      padding-left: 9.375vw;
      padding-right: 9.375vw;
    }

    h4 {
      font-size: 1.875vw;
      line-height: 1.5;
      text-align: center;
      margin-bottom: 2.2916vw;

      @media (max-width: $md1) {
        font-size: 5vw;
        line-height: 1.6;
        margin-bottom: 6.25vw;
    }
    }

    p {
      text-align: center;
      position: relative;

      &:after {
        content: '';
        width: 100%;
        height: 2px;
        background-color: #ff6d56;
        position: absolute;
        bottom: 0;
        left: 0;
      }
    }
  }

  .right {
    background:#131D4A;
    width: 50vw;
    height: 41.6vw;

    @media (max-width: $md1) {
      display: none;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      opacity: 0.4;
    }
  }
}