.header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 2;
    
  
    .container {
      padding-top: 1.5625vw;
      padding-bottom: 1.5625vw;
      display: flex;
      justify-content: space-between;
      align-items: center;

      @media (max-width: $md1) {
        padding-top: 6.25vw;
        padding-bottom: 6.25vw;
      }

      .logo {
        display: flex;
        align-items: center;
        color: #fff;
  
        svg {
          width: 3.90625vw;
          height: 3.28125vw;

          @media (max-width: $md1) {
            width: 11.25vw;
            height: 9.375vw;
          }
        }
    
        p {
          font-size: 2.2916vw;
          margin-left: 0.52083vw;
          text-transform: uppercase;

          @media (max-width: $md1) {
            font-size: 5vw;
            margin-left: 1.5625vw;
          }
        }
      }
      
      .social {
        display: flex;
       
        a {
          display: flex;
          margin-right: 0.52083vw;

          @media (max-width: $md1) {
            margin-right: 1.5625vw;
          }
  
          svg {
            width: 1.25vw;
            height: 1.25vw;

            @media (max-width: $md1) {
              width: 5vw;
              height: 5vw;
            }
            
            path {
                transition: 0.5s;
            }
          }
    
          &:last-child {
            margin-right: 0;
          }

          &:hover svg path {
              fill: #FF6D56 !important;
          }
        }
      }
    }
  
    
  
    
  }